import React, { useState, useEffect } from 'react';
import styled, { keyframes, css } from 'styled-components';
import { useGame } from '../contexts/GameContext';
import PlayerCardHeader from './PlayerCardHeader';
import PlayingCard, { CardHand, CardStack } from './PlayingCard';
import GameCoin, { CoinCollection } from './GameCoin';
import RaceTrack from './RaceTrack';
import { DiceRoller } from './GameDice';

const fadeIn = keyframes`
  0% { opacity: 0; transform: translateY(20px); }
  100% { opacity: 1; transform: translateY(0); }
`;

const slideInFromRight = keyframes`
  0% { transform: translateX(100%); opacity: 0; }
  100% { transform: translateX(0); opacity: 1; }
`;

const pulseGlow = keyframes`
  0%, 100% { box-shadow: 0 0 10px rgba(0, 212, 255, 0.3); }
  50% { box-shadow: 0 0 20px rgba(0, 212, 255, 0.6), 0 0 30px rgba(0, 212, 255, 0.2); }
`;

const GameContainer = styled.div`
  min-height: 100vh;
  padding-top: 140px; /* Account for fixed header */
  padding-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
  background: 
    radial-gradient(circle at 20% 30%, rgba(0, 212, 255, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(255, 56, 56, 0.05) 0%, transparent 50%),
    linear-gradient(135deg, #0c0c0c 0%, #1a1a2e 50%, #16213e 100%);
  ${css`animation: ${fadeIn} 0.8s ease-out;`}
`;

const StageContainer = styled.div`
  max-width: 1400px;
  margin: 0 auto;
  ${css`animation: ${fadeIn} 0.6s ease-out;`}
`;

const StageTitle = styled.h1`
  font-family: 'Orbitron', monospace;
  font-size: 2.5rem;
  color: #00d4ff;
  text-align: center;
  margin-bottom: 2rem;
  text-shadow: 
    0 0 20px #00d4ff,
    0 0 40px rgba(0, 212, 255, 0.5);
  ${css`animation: ${pulseGlow} 3s ease-in-out infinite;`}
`;

const StageDescription = styled.p`
  font-family: 'Rajdhani', sans-serif;
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
  margin-bottom: 3rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
`;

// Dealer Selection Stage Components
const DealerSelectionContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
`;

const CardSelectionArea = styled.div`
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(15px);
  border: 2px solid rgba(0, 212, 255, 0.3);
  border-radius: 20px;
  padding: 2rem;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 1rem;
  max-width: 900px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
`;

const SelectionStatus = styled.div`
  background: rgba(0, 0, 0, 0.6);
  padding: 1.5rem 2rem;
  border-radius: 15px;
  border: 2px solid rgba(255, 215, 0, 0.5);
  color: white;
  text-align: center;
  font-family: 'Rajdhani', sans-serif;
  font-size: 1.1rem;
  font-weight: 600;
  ${css`animation: ${slideInFromRight} 0.5s ease-out;`}
`;

// Storm Stage Components
const StormContainer = styled.div`
  display: grid;
  grid-template-columns: 1fr 300px;
  gap: 2rem;
  
  @media (max-width: 1024px) {
    grid-template-columns: 1fr;
  }
`;

const GamePlayArea = styled.div`
  display: flex;
  flex-direction: column;
  gap: 2rem;
`;

const DiscardPileArea = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(15px);
  border: 2px solid rgba(0, 212, 255, 0.3);
  border-radius: 20px;
  padding: 2rem;
  min-height: 200px;
`;

const PlayerHandArea = styled.div`
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  padding: 1rem;
`;

const GameSidebar = styled.div`
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
`;

const SidebarSection = styled.div`
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(15px);
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  padding: 1.5rem;
`;

const SectionTitle = styled.h3`
  font-family: 'Orbitron', monospace;
  color: #00d4ff;
  margin-bottom: 1rem;
  font-size: 1.2rem;
`;

// Lane Selection Stage Components
const LaneSelectionContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
`;

const LaneGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
  max-width: 800px;
  width: 100%;
`;

const LaneOption = styled.div`
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(15px);
  border: 3px solid ${props => props.selected ? '#00d4ff' : 'rgba(255, 255, 255, 0.2)'};
  border-radius: 15px;
  padding: 2rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  
  &:hover {
    transform: translateY(-5px);
    border-color: #00d4ff;
    box-shadow: 0 10px 30px rgba(0, 212, 255, 0.3);
  }
  
  ${props => props.selected && css`
    background: rgba(0, 212, 255, 0.1);
    animation: ${pulseGlow} 2s ease-in-out infinite;
  `}
`;

const LaneNumber = styled.div`
  font-family: 'Orbitron', monospace;
  font-size: 3rem;
  font-weight: bold;
  color: #00d4ff;
  margin-bottom: 1rem;
  text-shadow: 0 0 20px #00d4ff;
`;

const LaneDescription = styled.div`
  color: rgba(255, 255, 255, 0.8);
  font-family: 'Rajdhani', sans-serif;
  font-size: 1.1rem;
  line-height: 1.4;
`;

// Coin Stage Components
const CoinStageContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 2rem;
`;

const CoinPlacementArea = styled.div`
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(15px);
  border: 2px solid rgba(255, 215, 0, 0.3);
  border-radius: 20px;
  padding: 2rem;
  display: flex;
  justify-content: center;
  min-height: 300px;
`;

// Racing Stage Components
const RacingContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 2rem;
`;

const RaceControls = styled.div`
  display: flex;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
`;

const ActionButton = styled.button`
  padding: 15px 30px;
  border: none;
  border-radius: 10px;
  background: linear-gradient(45deg, #00d4ff, #0099cc);
  color: white;
  font-family: 'Rajdhani', sans-serif;
  font-weight: 700;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  text-transform: uppercase;
  letter-spacing: 1px;
  
  &:hover:not(:disabled) {
    background: linear-gradient(45deg, #0099cc, #00d4ff);
    transform: translateY(-2px);
    box-shadow: 0 8px 25px rgba(0, 212, 255, 0.4);
  }
  
  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
    transform: none;
  }
`;

const DiceContainer = styled.div`
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 15px;
  border: 2px solid rgba(255, 255, 255, 0.2);
`;

function GameBoard() {
  const { 
    currentRoom, 
    player, 
    gameState, 
    leaveRoom,
    // Game actions would be added here
  } = useGame();
  
  const [selectedLane, setSelectedLane] = useState(null);
  const [lastDiceRoll, setLastDiceRoll] = useState([1, 1]);

  const currentStage = currentRoom?.gameState?.currentStage || 'lobby';
  const players = currentRoom?.players || [];

  // Mock data for demonstration
  const playerHand = [
    { rank: 'A', suit: 'hearts' },
    { rank: 'Q', suit: 'spades' },
    { rank: '7', suit: 'diamonds' },
    { rank: 'K', suit: 'clubs' },
    { rank: '9', suit: 'hearts' }
  ];

  const discardPile = [
    { rank: '8', suit: 'spades' }
  ];

  const playerCoins = [
    { type: 'gold', value: 5 },
    { type: 'gold', value: 3 },
    { type: 'silver', value: 2 },
    { type: 'bronze', value: 1 }
  ];

  const mockPositions = {
    [player?.id]: 12,
    // Other players would have their positions
  };

  const rollDice = () => {
    const newValues = [
      Math.floor(Math.random() * 6) + 1,
      Math.floor(Math.random() * 6) + 1
    ];
    setLastDiceRoll(newValues);
  };

  const handleDiceRoll = (values) => {
    setLastDiceRoll(values);
    // Here you would emit the dice roll to the server
    console.log('Dice rolled:', values);
  };

  const renderStage = () => {
    switch (currentStage) {
      case 'dealer-selection':
        return (
          <DealerSelectionContainer>
            <StageTitle>Dealer Selection</StageTitle>
            <StageDescription>
              Each player selects 3 cards from the 18 available cards. 
              The player with the highest total becomes the first dealer.
            </StageDescription>
            
            <CardSelectionArea>
              {Array.from({ length: 18 }, (_, i) => (
                <PlayingCard
                  key={i}
                  rank={['7', '8', '9', '10', 'J', 'Q', 'K', 'A', 'A'][i % 9]}
                  suit={['hearts', 'diamonds', 'spades', 'clubs'][i % 4]}
                  size="medium"
                  interactive={true}
                  isPlayable={true}
                />
              ))}
            </CardSelectionArea>
            
            <SelectionStatus>
              Waiting for all players to select 3 cards...
            </SelectionStatus>
          </DealerSelectionContainer>
        );

      case 'storm':
        return (
          <StormContainer>
            <GamePlayArea>
              <StageTitle>Storm Stage</StageTitle>
              <StageDescription>
                Play cards strategically! Queens are wild, Sevens are toxic, Aces skip turns.
              </StageDescription>
              
              <DiscardPileArea>
                <div style={{ display: 'flex', gap: '2rem', alignItems: 'center' }}>
                  <div style={{ textAlign: 'center' }}>
                    <h4 style={{ color: 'white', marginBottom: '1rem' }}>Stock Pile</h4>
                    <CardStack cards={Array(20).fill({})} size="medium" />
                  </div>
                  
                  <div style={{ textAlign: 'center' }}>
                    <h4 style={{ color: 'white', marginBottom: '1rem' }}>Discard Pile</h4>
                    <PlayingCard
                      rank={discardPile[0]?.rank}
                      suit={discardPile[0]?.suit}
                      size="large"
                    />
                  </div>
                </div>
              </DiscardPileArea>
              
              <PlayerHandArea>
                <h4 style={{ color: 'white', marginBottom: '1rem', textAlign: 'center' }}>
                  Your Hand
                </h4>
                <CardHand
                  cards={playerHand}
                  size="large"
                  interactive={true}
                  playableCards={playerHand.filter(card => 
                    card.suit === discardPile[0]?.suit || 
                    card.rank === discardPile[0]?.rank ||
                    card.rank === 'Q'
                  )}
                />
              </PlayerHandArea>
            </GamePlayArea>
            
            <GameSidebar>
              <SidebarSection>
                <SectionTitle>Game Info</SectionTitle>
                <p style={{ color: 'rgba(255, 255, 255, 0.8)', marginBottom: '1rem' }}>
                  Current Player: <strong style={{ color: '#00d4ff' }}>Player Name</strong>
                </p>
                <p style={{ color: 'rgba(255, 255, 255, 0.8)' }}>
                  Cards in deck: <strong style={{ color: '#ffd700' }}>23</strong>
                </p>
              </SidebarSection>
              
              <SidebarSection>
                <SectionTitle>Special Rules</SectionTitle>
                <ul style={{ color: 'rgba(255, 255, 255, 0.8)', listStyle: 'none', padding: 0 }}>
                  <li style={{ marginBottom: '0.5rem' }}> Queens: Wild cards</li>
                  <li style={{ marginBottom: '0.5rem' }}> Sevens: Draw +2</li>
                  <li style={{ marginBottom: '0.5rem' }}> Aces: Skip turn</li>
                </ul>
              </SidebarSection>
            </GameSidebar>
          </StormContainer>
        );

      case 'lane-selection':
        return (
          <LaneSelectionContainer>
            <StageTitle>Lane Selection</StageTitle>
            <StageDescription>
              Choose your starting lane for the race. Lane 1 starts first but is most crowded.
            </StageDescription>
            
            <LaneGrid>
              {[1, 2, 3, 4].map(lane => (
                <LaneOption
                  key={lane}
                  selected={selectedLane === lane}
                  onClick={() => setSelectedLane(lane)}
                >
                  <LaneNumber>{lane}</LaneNumber>
                  <LaneDescription>
                    {lane === 1 && "Inside lane - Shortest distance"}
                    {lane === 2 && "Second lane - Good balance"}
                    {lane === 3 && "Third lane - Less congestion"}
                    {lane === 4 && "Outside lane - Most room"}
                  </LaneDescription>
                </LaneOption>
              ))}
            </LaneGrid>
            
            {selectedLane && (
              <ActionButton>
                Confirm Lane {selectedLane}
              </ActionButton>
            )}
          </LaneSelectionContainer>
        );

      case 'coin-stage':
        return (
          <CoinStageContainer>
            <StageTitle>Coin Placement</StageTitle>
            <StageDescription>
              Place your coins on the track to affect movement and strategy.
            </StageDescription>
            
            <div style={{ display: 'grid', gridTemplateColumns: '1fr 300px', gap: '2rem' }}>
              <CoinPlacementArea>
                <RaceTrack
                  players={players}
                  playerPositions={mockPositions}
                  showPositionMarkers={true}
                />
              </CoinPlacementArea>
              
              <SidebarSection>
                <SectionTitle>Your Coins</SectionTitle>
                <CoinCollection
                  coins={playerCoins}
                  size="large"
                  interactive={true}
                  groupByType={true}
                />
              </SidebarSection>
            </div>
          </CoinStageContainer>
        );

      case 'racing':
        return (
          <RacingContainer>
            <StageTitle>Racing Stage</StageTitle>
            <StageDescription>
              Roll dice and race to victory! Use strategy and luck to cross the finish line first.
            </StageDescription>
            
            <RaceTrack
              players={players}
              currentLap={1}
              totalLaps={currentRoom?.settings?.numberOfLaps || 3}
              playerPositions={mockPositions}
              speed={lastDiceRoll.reduce((a, b) => a + b, 0) * 10}
            />
            
            <DiceContainer>
              <DiceRoller 
                numberOfDice={currentRoom?.settings?.numberOfDice || 1}
                size="large"
                onRoll={handleDiceRoll}
              />
            </DiceContainer>
            
            <RaceControls>
              <ActionButton>Move Forward</ActionButton>
              <ActionButton>Use Card</ActionButton>
              <ActionButton>Pit Stop</ActionButton>
            </RaceControls>
          </RacingContainer>
        );

      default:
        return (
          <div style={{ textAlign: 'center', padding: '4rem' }}>
            <StageTitle>Game Starting...</StageTitle>
            <StageDescription>
              Preparing the race. Please wait for all players to be ready.
            </StageDescription>
            <ActionButton onClick={leaveRoom}>
              Return to Lobby
            </ActionButton>
          </div>
        );
    }
  };

  return (
    <GameContainer>
      <PlayerCardHeader 
        players={players}
        currentPlayer={player}
        gameState={currentRoom?.gameState}
      />
      
      <StageContainer>
        {renderStage()}
      </StageContainer>
    </GameContainer>
  );
}

export default GameBoard;
